export const isTypeValid = (file, rule) => {
  let isValid = false;
  if (file.type === rule) {
    isValid = true;
  }
  const splittedRule = rule.split('/');
  if (splittedRule[1] === '*' && file.type.indexOf(splittedRule[0]) === 0) {
    isValid = true;
  }

  return isValid;
};



