import React, { PureComponent } from 'react';
import Types from 'prop-types';
import classNames from 'classnames';
import { UploadImageStep, ProcessingStep, CompleteStep } from './steps';
import { postData, asyncFileRead, isSizeValid, generateErrorMessage, isTypeValid } from './utils';
import './Upload.less';
import { processIndicatorStatuses } from '../processIndicator/ProcessIndicator';

class Upload extends PureComponent {
  constructor(props) {
    super(props);
    this.dragCounter = 0;
    this.droppableElement = React.createRef();
    this.errorMessage = {
      413: props.s3tooLargeMessage || null,
      415: props.s3wrongTypeMessage || null,
      unknownError: props.s3failureText || null,
    };
    this.timeouts = null;

    this.state = {
      errorMessage: '',
      fileName: '',
      isComplete: false,
      isError: false,
      isImage: false,
      isProcessing: false,
      isSuccess: false,
      response: null,
      uploadedImage: null,
    };
  }

  componentDidMount() {
    if (this.droppableElement && this.droppableElement.current) {
      this.addDragHandlers(this.droppableElement);
    }
  }

  onDragLeave() {
    this.dragCounter = this.dragCounter - 1;
    if (this.dragCounter === 0) {
      this.setState({ isDroppable: false });
    }
  }
  onDragEnter() {
    this.dragCounter = this.dragCounter + 1;
    const { s1disabled } = this.props;
    if (this.dragCounter === 1 && !s1disabled) {
      this.setState({ isDroppable: true });
    }
  }

  onAnimationCompleted = status => {
    const { response } = this.state;
    // Success.
    if (status === processIndicatorStatuses[2]) {
      const { onSuccess } = this.props;
      this.setState(
        {
          isProcessing: false,
          isComplete: true,
        },
        onSuccess && onSuccess(response),
      );
    }
    // Failure.
    if (status === processIndicatorStatuses[1]) {
      const { onFailure } = this.props;
      this.setState(
        {
          errorMessage: generateErrorMessage(response.status, this.errorMessage),
          isProcessing: false,
          isComplete: true,
        },
        onFailure && onFailure(response),
      );
    }
  };

  asyncPost = file => {
    const formData = new FormData();
    formData.append(file.name, file);
    return postData(this.prepareHttpOptions(this.props.httpOptions), formData);
  };

  asyncResponse = (response, type) => {
    const { animationDelay } = this.props;
    this.timeouts = setTimeout(() => {
      this.setState({
        response,
        isError: type === 'error',
        isSuccess: type === 'success',
      });
    }, animationDelay);
  };

  prepareHttpOptions = httpOptions => {
    if (!httpOptions.url) {
      throw new Error('You must supply a URL to post image data asynchronously');
    }

    return httpOptions;
  };

  addDragHandlers($element) {
    $element.current.addEventListener('dragenter', ev => {
      ev.preventDefault();
      this.onDragEnter();
    });

    $element.current.addEventListener('dragover', ev => ev.preventDefault());

    $element.current.addEventListener('dragleave', ev => {
      ev.preventDefault();
      this.onDragLeave();
    });

    $element.current.addEventListener('drop', ev => {
      ev.preventDefault();
      this.fileDropped(ev.dataTransfer.files[0]);
    });
  }

  reset = ev => {
    if (ev) {
      ev.preventDefault();
    }

    clearTimeout(this.timeouts);
    this.dragCounter = 0;
    const { onCancel } = this.props;

    this.setState(
      {
        isComplete: false,
        isError: false,
        isProcessing: false,
        isSuccess: false,
      },
      onCancel && onCancel(),
    );
  };

  showDataImage = dataUrl => {
    const { isImage } = this.state;
    if (isImage) {
      this.setState({
        uploadedImage: dataUrl,
      });
    }
  };

  fileDropped = file => {
    const { httpOptions, maxSize, onStart, s1disabled, s1accept } = this.props;

    if (s1disabled) {
      return false;
    }

    if (!file) {
      throw new Error('Could not retrieve file');
    }

    if (onStart) {
      onStart(file);
    }

    this.setState({
      isImage: file.type && file.type.indexOf('image') > -1,
      fileName: file.name,
      isDroppable: false,
      isProcessing: true,
    });

    if (!isSizeValid(file, maxSize)) {
      const response = {
        status: 413,
        statusText: 'Request Entity Too Large',
      };
      return this.asyncResponse(response, 'error');
    }

    if (!isTypeValid(file, s1accept)) {
      const response = {
        status: 415,
        statusText: 'Unsupported Media Type',
      };
      return this.asyncResponse(response, 'error');
    }

    if (httpOptions) {
      // Post the file to provided endpoint
      return this.asyncPost(file)
        .then(response => this.asyncResponse(response, 'success'))
        .then(() => asyncFileRead(file))
        .then(response => this.showDataImage(response))
        .catch(error => this.asyncResponse(error, 'error'));
    }
    // Post on form submit. And return the encoded image.
    return asyncFileRead(file)
      .then(response => {
        this.showDataImage(response);
        this.asyncResponse(response, 'success');
      })
      .catch(error => this.asyncResponse(error, 'error'));
  };

  render() {
    const {
      s1dropMessage,
      s1accept,
      s1buttonText,
      s1disabled,
      s1helpImage,
      s1label,
      s1placeholder,
      s2buttonText,
      s2failureText,
      s2processingText,
      s2successText,
      s3buttonText,
      s3successText,
      size,
    } = this.props;

    const {
      errorMessage,
      fileName,
      isComplete,
      isDroppable,
      isError,
      isImage,
      isProcessing,
      isSuccess,
      uploadedImage,
    } = this.state;

    return (
      <div
        className={classNames({
          droppable: true,
          'droppable-sm': size === 'sm',
          'droppable-md': size === 'md' || !size,
          'droppable-lg': size === 'lg',
          'droppable-dropping': isDroppable,
          'droppable-processing': isProcessing,
          'droppable-complete': isComplete,
        })}
        ref={this.droppableElement}
      >
        {!isProcessing &&
          !isComplete && (
            <UploadImageStep
              fileDropped={file => this.fileDropped(file)}
              isComplete={isComplete}
              s1accept={s1accept}
              s1buttonText={s1buttonText}
              s1disabled={s1disabled}
              s1helpImage={s1helpImage}
              s1label={s1label}
              s1placeholder={s1placeholder}
              s1dropMessage={s1dropMessage}
            />
          )}

        {isProcessing && (
          <ProcessingStep
            isComplete={isComplete}
            isError={isError}
            isProcessing={isProcessing}
            isSuccess={isSuccess}
            onAnimationCompleted={this.onAnimationCompleted}
            onClear={this.reset}
            s2buttonText={s2buttonText}
            s2failureText={s2failureText}
            s2processingText={s2processingText}
            s2successText={s2successText}
          />
        )}
        {/* Starts render the step when isSuccess or isError are true so markup is there when css transition kicks in
        css transition to work properly */}
        {(isSuccess || isError || isComplete) && (
          <CompleteStep
            fileName={fileName}
            isComplete={isComplete}
            isError={isError}
            isImage={isImage}
            onClear={this.reset}
            s3buttonText={s3buttonText}
            s3failureText={errorMessage}
            s3successText={s3successText}
            uploadedImage={uploadedImage}
          />
        )}
      </div>
    );
  }
}

Upload.propTypes = {
  animationDelay: Types.number,
  httpOptions: Types.shape({ url: Types.string }),
  maxSize: Types.number,
  onCancel: Types.func,
  onFailure: Types.func,
  onStart: Types.func,
  onSuccess: Types.func,
  s1accept: Types.oneOf(['image/*', 'application/*']),
  s1buttonText: Types.string,
  s1disabled: Types.bool,
  s1dropMessage: Types.string,
  s1helpImage: Types.string,
  s1label: Types.string,
  s1placeholder: Types.string,
  s2buttonText: Types.string,
  s2failureText: Types.string,
  s2processingText: Types.string,
  s2successText: Types.string,
  s3buttonText: Types.string,
  s3failureText: Types.string,
  s3successText: Types.string,
  s3tooLargeMessage: Types.string,
  s3wrongTypeMessage: Types.string,
  size: Types.oneOf(['sm', 'md', 'lg']),
};

Upload.defaultProps = {
  animationDelay: 1000,
  httpOptions: null,
  maxSize: 5000000,
  onCancel: null,
  onFailure: null,
  onStart: null,
  onSuccess: null,
  s1accept: 'image/*',
  s1buttonText: 'Or Select File',
  s1disabled: false,
  s1dropMessage: 'Drop file to start upload',
  s1helpImage: '',
  s1label: '',
  s1placeholder: 'Drag and drop a file less than 5MB',
  s2buttonText: 'Cancel',
  s2failureText: 'Upload failed.Please, try again',
  s2processingText: 'Uploading...',
  s2successText: 'Upload complete!',
  s3buttonText: 'Select other file?',
  s3failureText: 'Upload failed.Please, try again',
  s3successText: 'Upload complete!',
  s3tooLargeMessage: 'Please provide a file smaller than 5MB',
  s3wrongTypeMessage: 'Please provide a supported format',
  size: 'md',
};

export default Upload;



