import React, { PureComponent } from 'react';
import Types from 'prop-types';
import Upload from '@transferwise/icons/react/upload';
import TopUp from '@transferwise/icons/react/top-up';

class UploadImageStep extends PureComponent {
  static propTypes = {
    fileDropped: Types.func.isRequired,
    isComplete: Types.bool.isRequired,
    s1accept: Types.string.isRequired,
    s1buttonText: Types.string.isRequired,
    s1dropMessage: Types.string.isRequired,
    s1disabled: Types.bool.isRequired,
    s1helpImage: Types.string.isRequired,
    s1label: Types.string.isRequired,
    s1placeholder: Types.string.isRequired,
  };

  constructor() {
    super();
    this.uploadInputRef = React.createRef();
  }

  onManualUpload = () => {
    const { fileDropped } = this.props;
    if (this.uploadInputRef && this.uploadInputRef.current) {
      const file = this.uploadInputRef.current.files[0];
      fileDropped(file);
    }
  };

  render() {
    const {
      isComplete,
      s1accept,
      s1buttonText,
      s1disabled,
      s1dropMessage,
      s1helpImage,
      s1label,
      s1placeholder,
    } = this.props;

    return (
      <div>
        <div className="droppable-default-card" aria-hidden={isComplete}>
          <div className="droppable-card-content m-b-4">
            <div className="m-b-3">
              {s1helpImage && (
                <img src={s1helpImage} alt={s1label} className="thumbnail text-xs-center" />
              )}
              {!s1helpImage && (
                <div className="circle circle-sm circle-inverse p-t-1">
                  <Upload size="md" />
                </div>
              )}
            </div>
            {s1label && <h4 className="m-b-1">{s1label}</h4>}
            {s1placeholder && <p className="m-b-3">{`${s1placeholder}`}</p>}
            <label className={`btn btn-primary btn-sm ${s1disabled ? 'disabled' : ''}`}>
              {s1buttonText ? (
                <span>{s1buttonText}</span>
              ) : (
                <span className="icon icon-upload m-r-0" />
              )}
              <input
                type="file"
                accept={s1accept}
                className="tw-droppable-input hidden"
                disabled={s1disabled}
                name="file-upload"
                onChange={() => this.onManualUpload()}
                ref={this.uploadInputRef}
                value=""
              />
            </label>
          </div>
        </div>

        <div className="droppable-dropping-card droppable-card">
          <div className="droppable-card-content">
            <div className="circle circle-sm p-t-1 text-info">
              <TopUp />
            </div>
            {s1dropMessage && <h4 className="m-t-3">{s1dropMessage}</h4>}
          </div>
        </div>
      </div>
    );
  }
}

export default UploadImageStep;



