import React from 'react';
import Types from 'prop-types';
import ProcessIndicator from '../../../processIndicator';
import {
  processIndicatorStatuses,
  processIndicatorSizes,
} from '../../../processIndicator/ProcessIndicator';

const ProcessingStep = props => {
  const {
    isComplete,
    isError,
    isProcessing,
    isSuccess,
    onAnimationCompleted,
    onClear,
    s2buttonText,
    s2failureText,
    s2processingText,
    s2successText,
  } = props;

  let statusIndex = 0;

  if (isError) {
    statusIndex = 1;
  }
  if (isSuccess) {
    statusIndex = 2;
  }

  return (
    <div className="droppable-processing-card droppable-card" aria-hidden={isComplete}>
      <div className="droppable-card-content m-b-4">
        <ProcessIndicator
          size={processIndicatorSizes[1]}
          status={processIndicatorStatuses[statusIndex]}
          onAnimationCompleted={status => onAnimationCompleted(status)}
        />
        <h4 className="m-t-3 m-b-3">
          {!isSuccess && !isError && isProcessing && s2processingText}
          {isSuccess && s2successText}
          {isError && s2failureText}
        </h4>
        {s2buttonText && (
          <button className="btn btn-default btn-sm" onClick={() => onClear()}>
            {s2buttonText}
          </button>
        )}
      </div>
    </div>
  );
};

ProcessingStep.propTypes = {
  isComplete: Types.bool.isRequired,
  isError: Types.bool.isRequired,
  isProcessing: Types.bool.isRequired,
  isSuccess: Types.bool.isRequired,
  onAnimationCompleted: Types.func.isRequired,
  onClear: Types.func.isRequired,
  s2buttonText: Types.string.isRequired,
  s2failureText: Types.string.isRequired,
  s2processingText: Types.string.isRequired,
  s2successText: Types.string.isRequired,
};

export default ProcessingStep;



